//import icons from '../img/icons.svg'



const recipeContainer = document.querySelector('.recipe');
//https://forkify-api.herokuapp.com/api/v2/recipes?search=pizza&key=e4db81ef-099e-4a7c-807b-91cec76aeb35



const loadingSpinner = (parentEl) => {
  const markup = `
       <div class="spinner">
          <svg>
            <use href="src/img/icons.svg#icon-loader"></use>
          </svg>
        </div>
  `
  parentEl.innerHTML = ''
  parentEl.insertAdjacentHTML('afterbegin', markup)
}


const fetchReciecpe = async() => {
  try {
      loadingSpinner(recipeContainer)
      const response = await fetch('https://forkify-api.herokuapp.com/api/v2/recipes/5ed6604591c37cdc054bc886')
      const data = await response.json()
      let {recipe} = data.data
      console.log(recipe)
      const {id, title, publihser, sourceUrl, image_url, servings, ingredients, cooking_time } = recipe


      const reciepeHTML = `
      <figure class="recipe__fig">
      <img src=${image_url} alt=${title} class="recipe__img" />
      <h1 class="recipe__title">
        <span>${title}</span>
      </h1>
    </figure>

    <div class="recipe__details">
      <div class="recipe__info">
        <svg class="recipe__info-icon">
          <use href="src/img/icons.svg#icon-clock"></use>
        </svg>
        <span class="recipe__info-data recipe__info-data--minutes">${cooking_time}</span>
        <span class="recipe__info-text">minutes</span>
      </div>
      <div class="recipe__info">
        <svg class="recipe__info-icon">
          <use href="src/img/icons.svg#icon-users"></use>
        </svg>
        <span class="recipe__info-data recipe__info-data--people">${servings}</span>
        <span class="recipe__info-text"></span>

        <div class="recipe__info-buttons">
          <button class="btn--tiny btn--increase-servings">
            <svg>
              <use href="src/img/icons.svg#icon-minus-circle"></use>
            </svg>
          </button>
          <button class="btn--tiny btn--increase-servings">
            <svg>
              <use href="src/img/icons.svg#icon-plus-circle"></use>
            </svg>
          </button>
        </div>
      </div>

      <div class="recipe__user-generated">
        <svg>
          <use href="src/img/icons.svg#icon-user"></use>
        </svg>
      </div>
      <button class="btn--round">
        <svg class="">
          <use href="src/img/icons.svg#icon-bookmark-fill"></use>
        </svg>
      </button>
    </div>

    <div class="recipe__ingredients">
      <h2 class="heading--2">Recipe ingredients</h2>
      <ul class="recipe__ingredient-list">
        ${ingredients.map(ingredient => {
           const {quantity, unit, description} = ingredient
           return `
             <li class="recipe__ingredient">
               <svg class="recipe__icon">
                 <use href="src/img/icons.svg#icon-check"></use>
               </svg>
               <div class="recipe__quantity">${quantity}</div>
               <div class="recipe__description">
                 <span class="recipe__unit">${unit}</span>
                 ${description}
               </div>
             </li>
           `
        }).join('')}
      </ul>
    </div>

    <div class="recipe__directions">
      <h2 class="heading--2">How to cook it</h2>
      <p class="recipe__directions-text">
        This recipe was carefully designed and tested by
        <span class="recipe__publisher">${publihser}</span>. Please check out
        directions at their website.
      </p>
      <a
        class="btn--small recipe__btn"
        href="${sourceUrl}"
        target="_blank"
      >
        <span>Directions</span>
        <svg class="search__icon">
          <use href="src/img/icons.svg#icon-arrow-right"></use>
        </svg>
      </a>
    </div>
      `
      recipeContainer.insertAdjacentHTML('afterbegin', reciepeHTML)
  } catch (error) {
      console.log(error)
  }
}

fetchReciecpe()

const timeout = function (s) {
  return new Promise( (_, reject) => {
    setTimeout(function () {
      reject(new Error(`Request took too long! Timeout after ${s} second`));
    }, s * 1000);
  });
};

